  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: rgb(0, 0, 0);
  /* text-shadow: 0 0 0.5rem rgb(255, 255, 255); */

}

.screen-wrapper h1{
  font-family: "NeueRegular";
  font-size: clamp(24px, 3vw, 35px);
  text-align: left;
  color:rgb(49, 69, 160);
  padding: 1vh;


}

.screen-wrapper h2{
  font-family: "NeueRegular";
  font-size: clamp(20px, 1.6vw, 30px);
  text-align: left;
  color:rgb(87, 87, 87);
  margin-top: 2vh;
  margin-bottom: 2vh;
  padding: 1vh;


}

